* {
  box-sizing: border-box;
}

body {
  text-align: center;
  background: #f0f4f8;
  margin: 0;
  font-family: Arial, sans-serif;
}

#app {
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  display: flex;
}

.page {
  text-align: center;
  background-color: #fff;
  border-radius: 8px;
  width: 100%;
  max-width: 420px;
  padding: 24px;
}

h1, h2 {
  color: #333;
}

.opciones {
  justify-content: center;
  gap: 20px;
  margin-top: 30px;
  display: flex;
}

game-option {
  cursor: pointer;
}

.result {
  margin-top: 16px;
  font-size: 24px;
}

button {
  cursor: pointer;
  color: #fff;
  background: #4da3ff;
  border: none;
  border-radius: 6px;
  padding: 15px 25px;
  font-size: 16px;
}

button:hover {
  background: #2d8cff;
}

.score {
  background: #fff;
  border-radius: 6px;
  width: 220px;
  margin: 15px auto;
  padding: 10px;
  box-shadow: 0 2px 4px #0000001a;
}

.score p {
  margin: 4px 0;
}
